// import Noty from "@/utils/helpers/noty";
import Crypt from "@/utils/helpers/crypt";
// import { mask } from "vue-the-mask";
import UploadService from "@/api/services/upload";
// import { VueEditor } from "vue2-editor";

export default {
  // directives: { mask },

  // components: {
  //   VueEditor
  // },

  computed: {
    columns: function() {
      return this.$store.getters[`${this.$route.meta.module}/columns`];
    },
  },

  data: () => ({
    loading: true,
    item: null,
    dialog: {
      status: false,
      id: null,
      name: null,
    },
    service: null,
    uploads: {
      image: null,
      file: null,
      thumb_image: null,
    },
    editorOption: [
      ["bold", "italic", "underline", "strike", "link"],
      [{ color: [] }, { background: [] }],
      [{ align: [] }],
      [{ size: ["small", false, "large", "huge"] }],
      [{ indent: "-1" }, { indent: "+1" }],
      [{ list: "ordered" }, { list: "bullet" }],
      ["clean"],
      ["link", "image", "video"],
    ],
    apiValues: {},
  }),

  methods: {
    readImage(column, file) {
      const self = this;
      var reader = new FileReader();
      reader.onload = (e) => {
        self.uploads[column] = e.target.result;
      };
      reader.readAsDataURL(file);
    },

    async getItem() {
      this.loading = true;
      const params = this.$route.params.id;

      await this.service.getId(params).then((response) => {
        if (
          response.data &&
          response.data.Results &&
          response.data.Results[0]
        ) {
          this.item = response.data.Results[0];
        }
      });
      this.loading = false;
    },

    backToList() {
      const route = this.$route.fullPath.split("/edit")[0];
      this.$router.push(route);
    },

    async prepareSave() {
      const self = this;
      if (!self.$refs.form.validate()) {
        self.loading = false;
        return false;
      }

      const valueObject = {};
      valueObject.id = self.item.id;
      const promises = self.$refs.form.inputs.map(async (item) => {
        if (item.value) {
          const inputAttr = item.$attrs;
          valueObject[inputAttr.name] = item.value;

          if (inputAttr.name === "pass") {
            valueObject[inputAttr.name] = new Crypt().CryptPassword(item.value);
          }

          if (
            inputAttr.type === "file" ||
            (Boolean(inputAttr.to) && typeof item.value !== "string")
          ) {
            const bodyFormData = await new FormData();
            bodyFormData.append("file", await item.value);

            await UploadService.save(item.$attrs.to, bodyFormData).then(
              async (response) => {
                if (
                  response &&
                  response.data &&
                  response.data.Results &&
                  response.data.Results[0]
                ) {
                  valueObject[inputAttr.name] = await response.data.Results[0];
                }
              },
            );
          }
        }
      });

      await Promise.all(promises);
      return valueObject;
    },

    async save() {
      const self = this;
      self.loading = true;

      await self.prepareSave().then((valueObject) => {
        self.service.edit(valueObject).then((response) => {
          if (response) {
            // Noty.success("Item editado");
            self.backToList();
          }
        });
      });
      self.loading = false;
    },

    async confirmDeletion() {
      this.setDialog({});
      const itemId = this.item.id;

      await this.service.delete(itemId).then((response) => {
        if (response) {
          // this.$noty.success("Deletado");
          this.backToList();
        }
      });
    },

    setDialog({ status, id, name }) {
      this.dialog = {
        status: status,
        id: id,
        name: name,
      };
    },

    getDialogObject() {
      return {
        status: false,
      };
    },

    async getService() {
      this.service = await import(
        `@/api/services/${this.$route.meta.module}`
      ).then((module) => {
        return module.default;
      });
    },

    async prepareApiSelects() {
      const self = this;
      if (self.$store.getters[`${self.$route.meta.module}/getSelect`]) {
        for (let select in self.$store.getters[
          `${self.$route.meta.module}/getSelect`
        ]) {
          await self.service.selectValues(select).then((response) => {
            self.apiValues[select] = response.data;
          });
        }
      }
    },
  },
  async created() {
    await this.getService();
    await this.prepareApiSelects();
    await this.getItem();
  },
};
